<template>
<gmap-map :center="center" :zoom="15">
  <gmap-info-window :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false">
    <p><a :href="webLink" target="_blank">{{business}}<i class="fa fa-external-link ml-2" aria-hidden="true"></i></a></p>
    <p><a :href="mapLink" target="_blank">{{infoContent}}<i class="fa fa-map-marker ml-2" aria-hidden="true"></i></a></p>
  </gmap-info-window>
  <gmap-marker :key="index" v-for="(m, index) in markers" :position="m.position" :clickable="true" :draggable="false" @click="toggleInfoWindow(m,i)"></gmap-marker>
</gmap-map>
</template>
<style lang="scss" scoped>
.vue-map-container {
    width: 100%;
    height: 100vh;
    height: calc(101vh - 50px);
}
.vue-map {
    p {
        margin: 0.5rem 0;
        font-size: 1rem;
    }
}
</style>
<script>
import * as VueGoogleMaps from 'vue2-google-maps';
import Vue from 'vue';
var markerIcon = require("../assets/logoBlackFull.png");
Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyB-hzCedQopupNEWRNzGCPufWStmEgH0Yo',
    v: 'OPTIONAL VERSION NUMBER',
    // libraries: 'places', //// If you need to use place input
  }
});
export default {
  data() {
    return {
      infoContent: '',
      business: '',
      mapLink: '',
      webLink: '',
      infoWindowPos: {
        lat: 0,
        lng: 0
      },
      infoWinOpen: false,
      currentMidx: null,
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      },
      center: {
        lat: 44.0508424,
        lng: -121.3087181
      },
      markers: [{
        position: {
          lat: 44.0508424,
          lng: -121.3125494
        },
        icon: markerIcon,
        mapLink: 'https://goo.gl/maps/tggfvP6J24u',
        webLink: 'https://spokenmoto.com/',
        business: 'Spoken Moto',
        infoText: '310 SW Industrial Way, Bend, OR 97702'
      }, {
        position: {
          lat: 44.0506168,
          lng: -121.3045604
        },
        mapLink: 'hhttps://goo.gl/maps/svHNzP5xA1J2',
        webLink: 'http://www.theworkhousebend.com/',
        business: 'The Worhouse',
        infoText: '50 SE Scott St #6, Bend, OR 97701'
      }]
    }
  },
  methods: {
    toggleInfoWindow: function(marker, idx) {

      this.infoWindowPos = marker.position;
      this.infoContent = marker.infoText;
      this.mapLink = marker.mapLink;
      this.webLink = marker.webLink;
      this.business = marker.business;

      //check if its the same marker that was selected if yes toggle
      if (this.currentMidx == idx) {
        this.infoWinOpen = !this.infoWinOpen;
      }
      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = true;
        this.currentMidx = idx;

      }
    }

  }
}
</script>



